import React, { Component } from "react";
import "./welcome_message.css";
import { typewriter } from "./functions.js";

class WelcomeMessage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: props.info.title,
      single_person: props.info.single_person,
    };
  }

  componentDidMount() {
    var invite_text = `s radou ${this.state.single_person ? " te " : " vas "} pozivamo da ${this.state.single_person ? " bude " : " budete "} dio naeg velikog dana!`;
    var text_to_write = new Array(
      `${this.state.title},`,
      invite_text
    );
    var element_id = "invite_text";
    typewriter(text_to_write, element_id);
  }
  

  render() {
    return (
      <div className="body">
        <div id="invite_text" className="text"></div>
      </div>
    );
  }
}

export default WelcomeMessage;
